<template>
  <v-card
    height="100%"
  >
    <div
      id="investigacion-investigadores-franjaetaria"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Franja etaria.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('investigacion-investigadores-franjaetaria')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as investigadores/as.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Franja etaria
                </th>
                <th class="text-left">
                  Femenino
                </th>
                <th class="text-left">
                  Masculino
                </th>
                <th class="text-left">
                  Total
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in franjaEtariaData"
                :key="index"
              >
                <td>{{ parseFranjaEtaria(index) }}</td>
                <td>{{ item.fem }}</td>
                <td>{{ item.masc }}</td>
                <td>{{ item.total }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>
<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['investigadoresCargos', 'mobileSize']),
    barChartOptions () {
      return {
        chartArea: {
          left: 170,
          right: 40,
          top: 40,
          bottom: 40
        },
        // tooltip: {
        //   isHtml: true,
        //   textStyle: { color: 'white', bold: false }
        // },
        colors: this.colors,
        title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        isStacked: 'absolute',
        legend: {
          position: 'top',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        bar: { groupWidth: '70%' },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        hAxis: {
          textPosition: 'none',
          format: '0',
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    barChartData () {
      return {
        cols: [
          { id: 'Franja etária', label: 'Franja etária', type: 'string' },
          { id: 'Femenino', label: 'Femenino', type: 'number' },
          { id: 'Masculino', label: 'Masculino', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 65, f: 'Mayores de 65 años' },
              {
                v:
                  this.franjaEtariaData.sesentaycinco.fem,
                f: this.franjaEtariaData.sesentaycinco.fem
              },
              {
                v: this.franjaEtariaData.sesentaycinco.masc,
                f: this.franjaEtariaData.sesentaycinco.masc
              }
            ]
          },
          {
            c: [
              { v: 64, f: 'De 55 a 64 años' },
              {
                v:
                  this.franjaEtariaData.sesentaycuatro.fem,
                f: this.franjaEtariaData.sesentaycuatro.fem
              },
              {
                v: this.franjaEtariaData.sesentaycuatro.masc,
                f: this.franjaEtariaData.sesentaycuatro.masc
              }
            ]
          },
          {
            c: [
              { v: 54, f: 'De 45 a 54 años' },
              {
                v:
                  this.franjaEtariaData.cincuentaycuatro.fem,
                f: this.franjaEtariaData.cincuentaycuatro.fem
              },
              {
                v:
                this.franjaEtariaData.cincuentaycuatro.masc,
                f:
                this.franjaEtariaData.cincuentaycuatro.masc
              }
            ]
          },
          {
            c: [
              { v: 44, f: 'De 35 a 44 años' },
              {
                v:
                  this.franjaEtariaData.cuarentaycuatro.fem,
                f: this.franjaEtariaData.cuarentaycuatro.fem
              },
              {
                v: this.franjaEtariaData.cuarentaycuatro.masc,
                f: this.franjaEtariaData.cuarentaycuatro.masc
              }
            ]
          },
          {
            c: [
              { v: 34, f: 'De 25 a 34 años' },
              {
                v:
                  this.franjaEtariaData.treintaycuatro.fem,
                f: this.franjaEtariaData.treintaycuatro.fem
              },
              {
                v: this.franjaEtariaData.treintaycuatro.masc,
                f: this.franjaEtariaData.treintaycuatro.masc
              }
            ]
          },
          {
            c: [
              { v: 25, f: 'Menores de 25 años' },
              {
                v:
                  this.franjaEtariaData.veinticinco.fem,
                f: this.franjaEtariaData.veinticinco.fem
              },
              {
                v: this.franjaEtariaData.veinticinco.masc,
                f: this.franjaEtariaData.veinticinco.masc
              }
            ]
          }
        ]
      }
    },
    franjaEtariaData () {
      return this.investigadoresCargos
        .reduce(
          (acc, cur) => {
            const edadNum = Number(cur.edad)
            const edad =
          edadNum <= 25
            ? 'veinticinco'
            : edadNum <= 34
              ? 'treintaycuatro'
              : edadNum <= 44
                ? 'cuarentaycuatro'
                : edadNum <= 54
                  ? 'cincuentaycuatro'
                  : edadNum <= 64
                    ? 'sesentaycuatro'
                    : 'sesentaycinco'
            return cur.sexo === 'M'
              ? {
                ...acc,
                [edad]: { ...acc[edad], masc: acc[edad].masc + 1, total: acc[edad].total + 1 }
              }
              : {
                ...acc,
                [edad]: { ...acc[edad], fem: acc[edad].fem + 1, total: acc[edad].total + 1 }
              }
          },
          {
            sesentaycinco: { masc: 0, fem: 0, total: 0 },
            sesentaycuatro: { masc: 0, fem: 0, total: 0 },
            cincuentaycuatro: { masc: 0, fem: 0, total: 0 },
            cuarentaycuatro: { masc: 0, fem: 0, total: 0 },
            treintaycuatro: { masc: 0, fem: 0, total: 0 },
            veinticinco: { masc: 0, fem: 0, total: 0 }
          }
        )
    }
  },
  methods: {
    parseFranjaEtaria (v) {
      return v === 'sesentaycinco'
        ? 'Mayores de 65 años'
        : v === 'sesentaycuatro'
          ? 'De 55 a 64 años'
          : v === 'cincuentaycuatro'
            ? 'De 45 a 54 años'
            : v === 'cuarentaycuatro'
              ? 'De 35 a 44 años'
              : v === 'treintaycuatro'
                ? 'De 25 a 34 años'
                : v === 'veinticinco'
                  ? 'Menores de 25 años'
                  : v
    }
  }
}
</script>
